<template>
  <div class="goodsList mescroll" id="list">
    <div class="list-data" id="listData">
      <div class="banner">
        <van-swipe :show-indicators="false" :autoplay="3000">
          <van-swipe-item v-for="(item,index) in picList" :key="index">
            <img :src="$utils.dealImg(item.pic)" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
      <form action="/">
        <van-search v-model="value" show-action action-text="搜索" @cancel="onSearch" placeholder="搜索" background="#fff" @search="onSearch" @blur="blur" />
      </form>
      <div class="mid">
        <div class="list">
          <div class="item" v-for="(item,index) in list" :key="index">
            <div class="left" @click="goDetail(item.id)">
              <img :src="$utils.dealImg(item.pic)" alt />
            </div>
            <div class="item-mid">
              <p class="title" @click="goDetail(item.id)">
                {{item.shop_name}}
                <span v-if="false">已售151件</span>
              </p>
              <div v-if="$route.query.type != '2'">
                <div class="price" @click="goDetail(item.id)">
                  <span class="label">市场价</span>
                  <span class="font">¥</span>
                  <span class="num">
                    <del>{{item.market_price?item.market_price.toFixed(2):'0.00'}}</del>
                  </span>
                </div>
                <div class="price1" @click="goDetail">
                  <span class="label">销售价</span>
                  <span class="font">¥</span>
                  <span class="num">{{item.sale_price?item.sale_price.toFixed(2):'0.00'}}</span>
                </div>
                <div class="price2" @click="goDetail" v-if="false">
                  <span class="label">实付价</span>
                  <span class="font">¥</span>
                  <span class="num">59.00</span>
                </div>
              </div>
              <div v-else class="price3">
                <span>¥</span>
                {{item.sale_price}}+{{item.market_price}}
                <span>兑换券</span>
              </div>
              <div class="btn" @click="seckill(item.id)">{{$route.query.type != '2'?'立即秒杀':'立即兑换'}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Search, Swipe, SwipeItem } from "vant";
import { getIndexGoodsList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Search.name]: Search,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem
  },
  data() {
    return {
      value: "",
      mescroll: null,
      list: [],
      picList: [],
      picUrl: ""
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    seckill(id) {
      this.$router.push({
        name: "goodsDetail",
        query: { id: id, type: this.$route.query.type, buy: 1 }
      });
    },
    goDetail(id) {
      this.$router.push({
        name: "goodsDetail",
        query: { id: id, type: this.$route.query.type }
      });
    },
    onSearch() {
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    getData() {
      getAdvertList({
        shop_info_id: this.$route.query.shopInfoId,
        shop_info_navigation_id: this.$route.query.id
      }).then(res => {
        if (res.code == 0) {
          this.picList = res.data.itemList;
        }
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        pageNum: pageNum,
        pageSize: pageSize,
        shop_info_id: this.$route.query.shopInfoId,
        shop_info_navigation_id: this.$route.query.id,
        keyword: this.value
      };
      getIndexGoodsList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.rows) {
            curPageData.rows.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.rows ? curPageData.rows.length : 0,
            curPageData.recordCount
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "商品列表";
    // this.loadData();
    setTimeout(() => {
      this.startMescroll();
    },600);
  }
};
</script>
<style lang="scss" scoped>
.goodsList {
  background: #fff;
  height: 100%;
  .top {
    padding: 10px;
    .pic {
      width: 100%;
      border-radius: 10px;
    }
  }
  .banner {
    position: relative;
    padding: 10px 0;
    .van-swipe-item {
      img {
        margin: 0 10px;
        width: calc(100% - 20px);
      }
    }
  }
  .list {
    .item {
      display: flex;
      padding: 10px 0;
      margin: 0 10px;
      border-bottom: 1pt solid #efefef;
      .left {
        img {
          width: 120px;
          height: 120px;
          border-radius: 5px;
          margin-right: 5px;
        }
      }
      .item-mid {
        flex: 2;
        position: relative;
        .title {
          font-size: 15px;
          line-height: 25px;
          span {
            float: right;
            font-size: 13px;
            color: #999;
          }
        }
        .price {
          padding: 5px 0;
          .label {
            font-size: 14px;
            color: #c3baac;
            background: #333;
            padding: 3px 5px;
            border-radius: 5px;
            margin-right: 10px;
            box-sizing: border-box;
            border: 1pt solid #333;
          }
          .font {
            font-size: 12px;
            color: #dbd0c1;
            margin-right: 3px;
          }
          .num {
            font-weight: bold;
            font-size: 16px;
            color: #dbd0c1;
          }
        }
        .price1 {
          padding: 5px 0;
          .label {
            font-size: 14px;
            color: #ef5852;
            border: 1pt solid #ef5852;
            padding: 3px 5px;
            border-radius: 5px;
            margin-right: 10px;
            box-sizing: border-box;
          }
          .font {
            font-size: 12px;
            color: #ef5852;
            margin-right: 3px;
          }
          .num {
            font-weight: bold;
            font-size: 16px;
            color: #ef5852;
          }
        }
        .price2 {
          padding: 5px 0;
          .label {
            font-size: 14px;
            color: #fff;
            background: #ef5852;
            padding: 3px 5px;
            border-radius: 5px;
            margin-right: 10px;
            box-sizing: border-box;
            border: 1pt solid #ef5852;
          }
          .font {
            font-size: 12px;
            color: #333;
            margin-right: 3px;
          }
          .num {
            font-weight: bold;
            font-size: 16px;
            color: #333;
          }
        }
        .price3 {
          color: #333;
          font-size: 15px;
          line-height: 30px;
          font-weight: bold;
          margin: 13px 0 0;
          span {
            font-size: 13px;
            font-weight: normal;
          }
        }
        .btn {
          position: absolute;
          right: 0;
          bottom: 5px;
          border: 1pt solid $base-color;
          color: $base-color;
          font-size: 13px;
          font-weight: bold;
          padding: 5px 8px;
        }
      }
    }
  }
}
</style>
<style lang="scss">
.goodsList {
  .mescroll-empty {
    padding-top: 0;
    transform: translateY(30%);
  }
}
</style>

